.articles {
	padding: 77px 0 63px;

	@include max(md) {
		padding: 60px 0 70px;
	}

	&__container {
		display: flex;
		flex-direction: column;
	}

	&__title {
		text-align: center;

		@include max(md) {
			order: -2;
		}
	}

	&__wrapper {
		display: flex;
		flex-wrap: wrap;
		margin: 25px -12px 0;

		@include max(lg) {
			margin-top: 18px;
		}
	}

	&__item {
		width: calc(33.3333% - 24px);
		margin: 0 12px;

		@include max(lg) {
			//height: 174px;
			margin-left: 0;
			margin-bottom: 0;
		}
	}

	&__more {
		margin: 17px auto 0;
		font-size: 18px;

		@include max(md) {
			order: -1;
			font-size: 16px;
			margin: 0 auto;
		}

		svg {
			width: 10px;
			height: 10px;
			margin-left: 7px;
		}
	}

	&__nav {
		width: 100%;
		display: none;
		align-items: center;
		justify-content: center;

		@include max(lg) {
			display: flex;
			margin-top: 25px;
		}

		@include max(xs) {
			justify-content: space-between;
		}
	}

	&__pagination {
		display: none;
		justify-content: center;
		position: relative;
		z-index: 1;

		@include max(lg) {
			display: flex;
			margin: 0 30px;
		}

		&.swiper-pagination-lock {
			display: none;
		}

		.swiper-pagination-bullet {
			display: block;
			width: 20px;
			height: 2px;
			background-color: $lightBlue;
			cursor: pointer;

			&-active {
				background-color: $blue;
			}
		}
	}

	&__prev,
	&__next {
		position: absolute;
		top: 52%;

		@include max(lg) {
			position: relative;
			top: unset;
			bottom: 0;
		}
	}

	&__prev {
		left: -45px;

		@include max(lg) {
			left: 0;
		}
	}

	&__next {
		right: -45px;

		@include max(lg) {
			right: 0;
		}
	}
}