.inner-page {
  $self: &;
  padding: 50px 0;

  @include max(sm) {
    padding: 30px 0;
  }

  &--flex {
    display: flex;
    flex-direction: column;
    min-height: 100vh;

    #{$self}__main {
      flex-grow: 1;
      display: flex;
      justify-content: center;

      & > * {
        align-self: center;
      }
    }
  }

  &__header {
    padding-bottom: 50px;

    @include max(sm) {
      padding-bottom: 30px;
    }

    &-row {
      display: flex;
      flex-wrap: wrap;
    }

    &-clear-cart {
      display: inline-block;
      vertical-align: top;
      margin-left: 30px;
      font-size: 15px;
      line-height: 1.2;
      font-weight: 500;
      color: $blue;
      transition: color 0.3s ease;

      align-self: center;

      @include hover {
        color: $blue-hover;
      }
    }
  }

  &__breadcrumbs {
    margin-bottom: 50px;

    @include max(sm) {
      margin-bottom: 30px;
    }

    &.no-margin {
      margin-bottom: 0;
    }
  }

  &__title {
    @include max(sm) {
      width: 100%;

      & + * {
        margin-left: 0 !important;
        margin-top: 16px;
      }
    }
  }

  &__filter {
    margin-top: 30px;
  }

  &__hero {
    margin-bottom: 22px;
  }

  &__features {
    margin-top: -22px;
  }

  &__contact {
    margin-top: 92px;

    @include max(sm) {
      margin-top: 72px;
    }
  }

  &__description {
    //padding-bottom: 54px;
    padding-right: 22px;
    font-size: 14px;
    line-height: 1.857142857142857;
    color: $gray-ph;

    @include max(md) {
      padding-right: 0;
    }

    img {
      @include max(md) {
        max-width: 50%;
        height: auto;
      }

      @include max(xs) {
        max-width: none;
        width: 100%;
        margin-right: 0 !important;
        margin-left: 0 !important;
      }
    }

    .float-left {
      margin-right: 24px;
      margin-bottom: 24px;
    }

    .float-right {
      margin-left: 24px;
      margin-bottom: 24px;
    }

    &::after {
      content: "";
      display: block;
      clear: both;
    }

    p + p {
      margin-top: 25px;
    }
  }

  &__courses {
    margin-top: 56px;
  }

  &__content {
    padding-bottom: 25px;
    font-size: 14px;
    line-height: 1.857142857142857;
    color: $gray-ph;

    p + p {
      margin-top: 25px;
    }

    &--columns {
      column-count: 2;

      @include max(sm) {
        column-count: 1;
      }
    }
  }

  &__illustrated {
    font-size: 14px;
    line-height: 1.857142857142857;
    color: $gray-ph;

    figure {
      @include max(md) {
        max-width: 50%;
        height: auto;
      }

      @include max(xs) {
        max-width: none;
        width: 100%;
        margin-right: 0 !important;
        margin-left: 0 !important;
      }
    }

    .float-left {
      margin-right: 24px;
      margin-bottom: 24px;
    }
    .float-right {
      margin-left: 24px;
      margin-bottom: 24px;
    }

    p + p {
      margin-top: 25px;
    }
  }

  &__calendar {
    margin-top: 50px;

    img {
      display: block;
      width: 100%;
      height: auto;
    }
  }

  &__cart-result {
    margin-top: 30px;
    margin-left: auto;
  }

  &__cart-promo {
    margin-bottom: 50px;
  }
}
