.info-block {
	background-color: $blue;
	position: relative;

	@include max(md) {
		padding-bottom: 10px;
	}

	&__bg {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;

		&::after {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			background: linear-gradient(270deg, #093981 20.3%, rgba(44, 96, 174, 0) 87.6%);
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			opacity: 0.2;
		}
	}

	&__container {
		display: flex;
		position: relative;
		bottom: 5px;

		@include max(md) {
			flex-direction: column;
		}
	}

	&__image {
		display: flex;
		align-self: flex-end;
		margin-top: -94px;
		margin-right: 72px;

		@include max(md) {
			margin: -44px auto 0;
		}

		img {
			position: relative;
			bottom: -5px;
		}
	}

	&__content {
		padding: 48px 0 20px;

		@include max(md) {
			padding: 25px 16px 20px;
		}
	}

	&__title {
		font-size: 24px;
		line-height: 32px;
		font-weight: 700;
		color: $white;

		@include max(md) {
			font-size: 18px;
			line-height: 24px;
		}
	}

	&__more {
		width: 170px;
		height: auto;
		padding: 12px;
		margin-top: 22px;
		font-size: 15px;

		@include max(md) {
			width: 138px;
			margin: 27px auto 0;
			padding: 16px 12px;
		}

		svg {
			width: 12px;
			height: 12px;
			padding-left: 0;
			margin-left: 5px;
		}
	}
}