.recommend {
	padding: 15px 0 61px;

	@include max(md) {
		padding: 19px 0 50px;
	}

	&__title {
		font-weight: 400;
		text-align: center;

		@include max(md) {
			font-weight: 500;
		}

		br {
			display: none;

			@include max(xs) {
				display: block;
			}
		}
	}

	&__wrapper {
		display: flex;
		margin-top: 48px;
		position: relative;

		@include max(md) {
			flex-direction: column;
			margin-top: 3px;
		}
	}

	&__tabs {
		width: 25%;

		@include max(md) {
			display: none;
		}

		p {
			font-size: 15px;
			line-height: 18px;
			font-weight: 500;
			cursor: pointer;
			transition: font-size 0.3s ease;

			+ p {
				margin-top: 23px;
			}

			&.active {
				font-size: 24px;
			}
		}
	}

	&__content {
		width: 75%;

		@include max(md) {
			width: 100%;
			margin-top: 30px;
		}
	}

	&__content-item {
		display: none;

		&.active {
			display: block;
		}
	}

	&__item {
		.product-preview {
			&__image {
				height: 189px;

				img {
					object-fit: none;
				}
			}

			&__title {
				margin-top: 2px;
			}

			&__text {
				margin-top: 7px;
			}

			&__bottom {
				justify-content: flex-start;
			}

			&__price {
				margin-left: 17px;
				color: $gray-26;
			}
		}
	}

	&__more {
		padding: 16px;
		margin-top: 20px;
		font-size: 18px;

		@include max(md) {
			display: none;
		}

		&--mobile {
			display: none;

			@include max(md) {
				display: block;
				margin: 5px auto 0;
				font-size: 16px;
				line-height: 20px;
			}
		}
	}

	&__nav {
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: center;

		@include max(lg) {
			margin-top: 12px;
		}

		@include max(xs) {
			justify-content: space-between;
		}
	}

	&__pagination {
		display: none;
		justify-content: center;
		position: relative;
		z-index: 1;

		@include max(lg) {
			display: flex;
			margin: 0 30px;
		}

		&.swiper-pagination-lock {
			display: none;
		}

		.swiper-pagination-bullet {
			display: block;
			width: 20px;
			height: 2px;
			background-color: $lightBlue;
			cursor: pointer;

			&-active {
				background-color: $blue;
			}
		}
	}

	&__prev,
	&__next {
		position: absolute;
		bottom: 16px;

		@include max(lg) {
			position: relative;
			bottom: 0;
		}
	}

	&__prev {
		right: 52px;

		@include max(lg) {
			right: unset;
			left: 0;
		}
	}

	&__next {
		right: 0;

		@include max(lg) {
			right: 0;
		}
	}

	.choices {
		display: none;
		z-index: 2;

		@include max(md) {
			display: block;
		}

		&::after {
			right: 20px !important;
		}

		&__inner {
			border: 1px solid $gray;
			padding: 13px 20px 13px 20px !important;
		}
	}
}