.courses-card {
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	height: 524px;
	padding: 68px 35px 30px;
	position: relative;
	border-radius: 8px;
	overflow: hidden;
	box-sizing: border-box;

	@include max(md) {
		height: 487px;
		padding: 68px 21px 30px;
	}

	&__stock {
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 5px 18px;
		border-radius: 0 5px 5px 0;
		background-color: $white;
		font-size: 19px;
		font-weight: 700;
		color: $blue;
		position: absolute;
		top: 34px;
		left: 0;
		z-index: 1;

		@include max(md) {
			padding: 8px 10px;
			top: 30px;
			font-size: 14px;
			line-height: 17px;
		}

		svg {
			width: 20px;
			height: 20px;
			fill: $blue;
			margin-left: 9px;

			@include max(md) {
				margin-left: 5px;
			}
		}
	}

	&__bg {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: $blue;

		&::after {
			content: '';
			display: block;
			width: 100%;
			height: 70%;
			position: absolute;
			left: 0;
			bottom: 0;
			background: linear-gradient(360deg, #1B498E 49.16%, rgba(44, 96, 174, 0) 100%);

			@include max(md) {
				height: 100%;
			}
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			object-position: top;
			opacity: 0.8;

			@include max(md) {
				object-position: center;
			}
		}
	}

	&__content {
		position: relative;
		color: $white;
	}

	&__title {
		font-size: 24px;
		line-height: 32px;
		font-weight: 700;

		@include max(md) {
			font-size: 16px;
			line-height: 20px;
			text-align: center;
		}
	}

	&__limit {
		font-size: 18px;
		line-height: 24px;

		@include max(md) {
			display: flex;
			justify-content: center;
			font-size: 14px;
			line-height: 17px;
			margin-top: 9px;
		}

		span.year {
			padding-right: 20px;

			@include max(md) {
				padding-right: 38px;
			}
		}
	}

	&__desc {
		font-size: 14px;
		line-height: 26px;
		margin-top: 13px;

		@include max(md) {
			font-size: 15px;
			line-height: 22px;
			margin-top: 24px;
		}

		br {
			@include max(lg) {
				display: none;
			}
		}
	}

	&__row {
		display: flex;
		align-items: center;
		margin-top: 36px;

		@include max(md) {
			flex-direction: column;
			margin-top: 18px;
		}
	}

	&__more {
		width: 170px;
		height: auto;
		padding: 9px 15px;
		margin-right: 21px;
		font-size: 15px;

		@include max(md) {
			width: 140px;
			order: 1;
			padding: 11px 15px;
			margin-top: 9px;
		}

		svg {
			width: 10px;
			height: 17px;
			margin-left: 5px;
		}
	}

	&__price {
		font-size: 19px;
	}

	&__gift {
		text-align: center;
		margin-top: 20px;

		a {
			text-decoration: underline;
			color: $white;

			@include hover {
				text-decoration: none;
			}
		}
	}
}